import { soundCtrl } from "./soundCtrl"

/**
 * config
 * 
 * 
 * 
 */
 const config = {
    _mute:true,//
    // mute:true,//
}

Object.defineProperty(config, 'mute', {
    get() {
        return config._mute
    },

    set(v) {
        config._mute = v
        if (!v) {
            soundCtrl.stopSound()
        } else {
            soundCtrl.playSound()
        }
        
    }
})

export default config

